@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;

  /*
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  color: white;
}

ul{
  list-style-type:none;
}

li {
  color: white;
}

h2{
  color:#ff4654;
}
.App {
  display: flex;
  flex-flow: column;
}
  
.Header { 
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  height: 10vh;   
  background-color: #ff4654;
}

.App-logo {
    height: 60px;
    width: 60px;
    margin-left:5px;
    pointer-events: none;
  }
  
.Navigation-bar ul {
  padding: 0px;
  margin: 0px;
  text-align: center;
  display:inline-block;
  vertical-align:top;
}

.Navigation-bar li {
  list-style-type: none;
  padding: 0px;
  height: 24px;
  margin-top: 4px;
  margin-bottom: 4px;
  display: inline;
}

.Navigation-bar li a {
  color:white;
  font-size: 16px;
  font-style: italic;
  font-weight: bold;
  text-decoration: none;
  line-height: 70px;
  padding: 5px 15px;
}

.Content { 
  display: flex;
  background-color: #18212A;
  height: 80vh;
}

.Footer { 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  background-color: #18212A;
}

.Footer a{
  color: #ff4654;
  text-decoration: none;
}
  
.Footer a:visited{
  text-decoration: none;
}

.Team-generator {
  width: 100%;
  display:flex;
  flex-flow: row;
  justify-content: space-between;
  margin-top:20px;
}
.Team {
  display:flex;
  flex-flow: column;
}

.Player-list{
  display: flex;
  flex-flow: column;
  align-items: center;
}

.Team {
  display: flex;
  flex-flow: column;
  align-items: center;
}

.Generate-teams{
  margin-bottom: 10px;
}

.Add-player-wrapper{
  display: flex;
  margin-top: 10px;
}

.Player-input {
  height: 80%;
  margin-right: 5px;
}

.Save-Player{
 
}

@media only screen and (max-width: 600px) {
  .Team-generator{
    display: flex;
    flex-flow: column;
  }
}




